    const supabase = createClient(supabaseUrl, supabaseKey);

    async function getUniqueFlightIds() {
        try {
            const { data, error } = await supabase.from('unique_flight_id').select('flight');

            if (error) {
                throw new Error(`Supabase error: ${error.message}`);
            }

            const uniqueFlightIds = data.map(row => row.flight.trim().toLowerCase()).sort();
            return uniqueFlightIds;
        } catch (error) {
            console.error('Error:', error.message);
        }
    }


    async function getCoordinateData() {
        try {
            const { data, error } = await supabase.from('results').select('lat, lon, flight');

            if (error) {
                throw new Error(`Supabase error: ${error.message}`);
            }

            // Map the data to an array of objects
            const coordinateData = data.map(row => ({
                lat: row.lat,
                lon: row.lon,
                flight: row.flight ? row.flight.trim().toLowerCase() : ""
            }));

            return coordinateData;
        } catch (error) {
            console.error('Error:', error.message);
        }
    }




    async function getCoordinateDataForFlight(flight) {
        try {
            const { data, error } = await supabase
                .from('results')
                .select('lat, lon, flight')
                .ilike('flight', `%${flight.trim()}%`);

            if (error) {
                throw new Error(`Supabase error: ${error.message}`);
            }

            // Map the data to an array of objects
            const coordinateData = data.map(row => ({
                lat: row.lat,
                lon: row.lon,
                flight: row.flight ? row.flight.trim().toLowerCase() : ""
            }));

            return coordinateData;
        } catch (error) {
            console.error('Error:', error.message);
        }
    }


    const uniqueFlightIds = await getUniqueFlightIds();
    const coordinateData = await getCoordinateData();



    function buildRadioButtons(options) {
        const container = document.getElementById('container');

        // Create search bar
        const searchBar = document.createElement('input');
        searchBar.type = 'text';
        searchBar.classList.add('search-bar');
        searchBar.id = 'search';
        searchBar.placeholder = 'Search...';

        // Append search bar to the container
        container.appendChild(searchBar);

        // Create radio buttons and wrap each group in a div
        const radioButtonGroups = options.map(option => {
            const radioButton = document.createElement('input');
            radioButton.type = 'radio';
            radioButton.name = 'flightRadio'; // Use the same name for radio buttons to make them exclusive
            radioButton.value = option;
            radioButton.id = option;

            const label = document.createElement('label');
            label.htmlFor = option;
            label.textContent = option;

            let tmpID = `group-${option}`
            const radioButtonGroup = document.createElement('div');
            radioButtonGroup.classList.add('radio-button-group', 'visible');
            radioButtonGroup.id = tmpID;
            radioButtonGroup.appendChild(radioButton);
            radioButtonGroup.appendChild(label);

            return radioButtonGroup;
        });

        // Append radio button groups to the container
        radioButtonGroups.forEach(radioButtonGroup => {
            container.appendChild(radioButtonGroup);
        });

        // Add live search functionality
        searchBar.addEventListener('input', function () {
            const searchTerm = searchBar.value.toLowerCase();

            radioButtonGroups.forEach(radioButtonGroup => {
                const optionText = radioButtonGroup.textContent.toLowerCase();
                const isVisible = optionText.includes(searchTerm);

                radioButtonGroup.classList.toggle('visible', isVisible);
            });
        });
    }

    buildRadioButtons(uniqueFlightIds);

    document.getElementById('toggle').addEventListener('click', function() {
    let container  = document.getElementById('container');
    if (container.classList.contains("open")) {
        container.classList.remove('open')
    } else {
        container.classList.add('open')
    }
    })
    function filterFlightOptionsDOM() {
        const searchQuery = document.getElementById('search').value.toLowerCase();

        uniqueFlightIds.forEach(flight => {
        let id = `group-${flight}`
            const flightElement = document.getElementById(id);
            const isVisible = flight.toLowerCase().includes(searchQuery);
            flightElement.style.display = isVisible ? 'block' : 'none';
        });
    }
    document.getElementById('search').addEventListener('input', filterFlightOptionsDOM);












    let map;
    let markers = [];


    function createMarkers(flightData) {
        for (let i = 0; i < flightData.length; i++) {
            let flightInfo = flightData[i];
            let marker = new google.maps.Marker({
                position: { lat: flightInfo.lat, lng: flightInfo.lon },
                map: map,
                title: flightInfo.flight,
                visible: true,
            });
            markers.push(marker);
        }
    }





    function initMap(flights) {
        map = new google.maps.Map(document.getElementById('map'), {
            center: { lat: 40.0150, lng: -105.2705 },
            zoom: 8,
        });
        // Create markers for each flight
        createMarkers(flights);
    }
    initMap(coordinateData)



